body {
    display: block ;

    padding-top: 0.2em ;
    padding-bottom: 0.2em ;
    padding-left: 0.3em ;
    padding-right: 0.3em ;
}

body > * {
    min-width: 10em ;
    width: 100% ;
    max-width: 50em ;
}

@media only screen and (min-width: 40em) {
    body {
        padding-left: 5em ;
        padding-right: 5em ;
    }
}

@media only screen and (min-height: 40em) {
    body {
        padding-top: 2em ;
        padding-bottom: 3em ;
    }
}

.post-list {
    list-style-type: none ;
    margin: 0 ;
    padding: 0 ;
}
.post-list > li {
    margin-top: 0.5em ;
    margin-bottom: 0.5em ;
}

.site-header, .site-footer { border: initial ; }

.site-header { min-height: 30px ; }
.site-header .wrapper div { position: relative ; }


.page-content { padding: 0 ; }
.page-content { flex: initial ; }

.home {
    text-align: center ;
    padding-bottom: 2em ;
    margin-bottom: 2em ;
    border-style: none none solid none ;
    border-width: 1px ;
}

.post-list-heading { text-align: center }

.post-filter { margin-top: 10px ; margin-bottom: 10px ; }

.post-content { margin-bottom: 0 ; }
.post-nav { margin-top: 30px ; }

.prev { float: left }
.next { float: right }

.poem .post-content { display: inline-block ; }

.post-meta { text-align: left ; }
.p-author::before { content: "• " ; }
.poem .p-author::before { content: none ; }
